.weather{
    width:300px ;
    border-radius: 15px ;
    box-shadow: 10px -2px 20px 2px rgba(0, 0, 0, 0.808);
    color: white;
    background-color:rgba(11, 20, 66, 0.84);
    margin: 20px auto 0 auto;
    padding: 0 20px 20px  20px;
}
.top,.bottom{
    display: flex;
    justify-content: space-between;
    align-items: center;
 
}
.temperature{
  color:darkgreen ;
}
.city{
    font-weight: 800;
    font-size: 18px;
    line-height: 1;
    margin: 0;
    letter-spacing: 1px;
}
.weather-description{
    font-weight: 250;
    font-size: 12px;
    line-height:1;
    margin: 0;
}
.weather-icon{
    width: 100px;
}
.temperature {
    font-weight: 600;
    font-size: 70px;
    width: auto;
    letter-spacing: -5px;
    margin: 10px 0;
  }
  
  .details {
    width: 100%;
    padding-left: 20px;
  }
  
  .parameter-row {
    display: flex;
    justify-content: space-between;
  }
  
  .parameter-label {
    text-align: left;
    font-weight: 400;
    font-size: 12px;
  }
  
  .parameter-value {
    text-align: right;
    font-weight: 600;
    font-size: 12px;
  }
  

